
<div :class="cls">
  <div class="c-list-header" v-if="header || $slots.header">
    <slot name="header">{{ header }}</slot>
  </div>
  <div>
    <ul class="c-list-content">
      <slot></slot>
    </ul>
  </div>
  <div class="c-list-footer" v-if="footer || $slots.footer">
    <slot name="footer">{{ footer }}</slot>
  </div>
</div>
