
<span :class="`c-breadcrumb-item`">
  <Link v-if="isLink" :href="href" :to="to" :replace="replace" :underline="false">
    <slot></slot>
  </Link>

  <span v-else :class="`c-breadcrumb-item--inner`">
    <slot></slot>
  </span>

  <span :class="separatorClass">
    {{ breadcrumb.separator }}
  </span>
</span>
