
<div :class="cls" ref="bar">
  <div
    class="c-tab-bar-item"
    :class="{
      'c-tab-active': active === v.name,
    }"
    ref="items"
    v-for="v in items"
    :key="v.name"
    @click="onClick(v.name)"
  >
    {{ v.label }}
  </div>
  <div class="c-tab-bar-line" :style="lineStyle"></div>
</div>
