
<div class="c-input">
  <input
    v-bind="$attrs"
    :class="cls"
    :disabled="itemDisabled"
    :placeholder="placeholder"
    @input="onInput"
    @change="onChange"
    @blur="onBlur"
    :maxlength="maxlength"
    :type="type"
    :value="value"
    @keyup.enter="onEnter"
  >
  <span v-if="isWordLimitVisible" class="c-input-tip">{{textNum}}/{{maxlength}}</span>
</div>
