
<label class="c-checkbox-all" :class="clsAll">
  <span :class="classes" class="c-checkbox">
    <span class="c-checkbox-inner" :class="clsinner"></span>
    <!-- 复选框组 -->
    <input
      v-if="group"
      class="c-checkbox-input"
      :class="classesinput"
      type="checkbox"
      :name="name"
      v-model="model"
      :value="label"
      :disabled="itemDisabled"
      @change="change"
    />
    <!-- 单个复选框 -->
    <input
      v-else
      class="c-checkbox-input"
      :class="classesinput"
      type="checkbox"
      :name="name"
      :checked="checkValue"
      :disabled="itemDisabled"
      @change="change"
    />
  </span>
  <slot>
    <span>{{ label }}</span>
  </slot>
</label>
