
<div :class="cls">
  <img
    :src="src"
    class="c-image-img"
    :style="imageStyle"
    @click="clickImage"
    :alt="alt"
    @error="handleError"
    @load="handleLoad"
  />
  <template v-if="preview">
    <img-preview
      :src="previewSrc && previewSrc !== '' ? previewSrc : src"
      v-show="showView"
      @close="closeView"
    ></img-preview>
  </template>
</div>
